* {
    box-sizing: border-box;
}

::selection {
    background-color: #D5D4D2;
    color: $clr1;
}

html {
    font-family: WorkSans;
    line-height: 1.6;
    color: $clr6;
}

body {
    margin: 0;
    max-width: 100%;
    margin: auto;
}

h1, h2, h3, h4, h5, h6 {
    font-family: WorkSans;
    margin: 0 0 0.6em;
    line-height: 1;
}

img {
    width: 100%;
}

p {
    margin: 0 0 1em;
}

a {
    color: $clr1;
    text-decoration: none;
}
a:hover {
    text-decoration: none;
}

hr {
    border: 0;
    height: 1px;
    background: #EFEFF0;
}